<template>
  <section class="todoapp">
    <!-- <header class="header">
      <input type="text" class="new-todo" @keyup.enter="addTodo" v-model="newTodo">
    </header> -->
    <section class="main">
      <!-- <input type="checkbox" class="toggle-all" id="toggle-all" v-model="allDone">
      <label for="toggle-all">Mark all as complete</label> -->

      <ul class="todo-list">
        <li class="todo" v-for="(todo) in filtertodo" :key="todo.id"><!--for循环要key绑定，保证唯一性 -->
          <div class="view">
            <input type="checkbox" class="toggle" v-model="todo.completed">
            <label>{{ todo.title }}</label>
            <button class="destroy" @click="removeTodo(todo)"></button>
          </div>
        </li>
      </ul>
    </section>
    <footer class="footer" v-show="todos.length" v-cloak>
      <span class="todo-count">
        <strong>{{ left }}</strong> left
      </span>
      <span class="todo-choose">
        <button class="clear-completed todo-do" @click="showAll">All</button>
        <button class="clear-completed todo-do" @click="showActive">Active</button>
        <button class="clear-completed todo-do" @click="showCompleted">Completed</button>
      </span>
      <button class="clear-completed" @click="removeTo">
        Clear completed
      </button>
    </footer>
  </section>
</template>

<script>
import { reactive, toRefs, computed, defineComponent } from 'vue'
export default defineComponent({
  props: {
    todolist: Object
  },
  setup(props) {
    const todos = [];
    console.log(props.todolist);
    props.todolist.forEach((element, ind) => {
      todos.push({ id: ind + 1, title: element, completed: false });
    });
    console.log(todos);
    const state = reactive({
      newTodo: '',
      todos: todos,
      filtertodo: todos
    })

    const addTodo = () => {
      if (!state.newTodo) return
      state.filtertodo.push({
        id: state.filtertodo.length + 1,
        title: state.newTodo,
        completed: false
      })
      state.todos.push({
        id: state.todos.length + 1,
        title: state.newTodo,
        completed: false
      })
      state.newTodo = ''
    }

    const removeTodo = (item) => {
      // state.todos.splice(idx, 1)
      state.filtertodo = state.todos.filter(todo => todo.id !== item.id)
      state.todos = state.todos.filter(todo => todo.id !== item.id)
    }
    const removeTo = () => {
      state.filtertodo = state.todos.filter(todo => !todo.completed)
    }

    const remaining = computed(
      () => state.todos.filter(todo => !todo.completed).length
    )

    const allDone = computed({
      get: function () {
        return remaining.value === 0
      },
      set: function (value) {
        state.filtertodo.forEach((todo) => {
          todo.completed = value
        })
      }
    })

    const left = computed(() => {
      return remaining.value
    })

    const showAll = () => {
      return state.filtertodo = state.todos.filter(todo => !todo.completed || todo.completed)
    }

    const showActive = () => {
      state.filtertodo = state.todos.filter(todo => !todo.completed)
    }

    const showCompleted = () => {
      state.filtertodo = state.todos.filter(todo => todo.completed)
    }

    return {
      ...toRefs(state),
      addTodo,
      removeTodo,
      allDone,
      left,
      removeTo,
      showAll,
      showActive,
      showCompleted,
    }
  }
});
</script>

